*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'TT Norms';
  src: url("../fonts/TTNorms-Regular/TTNorms-Regular.woff2") format("woff2"), url("../fonts/TTNorms-Regular/TTNorms-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'TT Norms';
  src: url("../fonts/TTNorms-Bold/TTNorms-Bold.woff2") format("woff2"), url("../fonts/TTNorms-Bold/TTNorms-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Bebas Neue';
  src: url("../fonts/BebasNeueBook/BebasNeueBook.woff2") format("woff2"), url("../fonts/BebasNeueBook/BebasNeueBook.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'TT Norms', sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

.content {
  background-image: linear-gradient(to bottom, #194d4d 0%, #303030 100%);
  flex: 1 1 auto;
}

.content__wrapper {
  padding: 30px 15px;
  background-color: #335252;
  border-radius: 15px;
  box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.1);
}

.content__container {
  max-width: 450px;
  padding: 0 30px;
  width: 100%;
  margin: 0 auto;
}

.form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.form__title {
  font-size: 20px;
  color: #fff;
  line-height: 1.2;
  letter-spacing: 0.04em;
  text-align: center;
}

.form__content {
  padding: 15px 37px;
}

.form__btn {
  margin: 25px 0;
}

.form__btn_enter {
  margin-bottom: 0;
}

.form__agree {
  font-size: 13px;
  text-align: center;
  color: #fff;
}

.form__agree.checkbox {
  text-align: left;
}

.form-item-filled .form-item__placeholder {
  bottom: 38px;
  font-size: 12px;
}

.form-item__label {
  height: 64px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 5px 0 10px 0;
}

.form-item__placeholder {
  position: absolute;
  left: 0;
  bottom: 18px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.5);
  transition: .3s bottom linear, .3s font-size linear;
}

.form-item__input {
  width: 100%;
  line-height: 1.2;
  box-shadow: none;
  position: relative;
  color: white;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.26);
  border-radius: 0;
  padding: 0 0 8px 0;
  font-size: 14px;
  cursor: initial;
  background-color: transparent;
  transition: .3s border-color linear;
  outline: none;
}

.form-item__input:focus {
  border-color: #3b3b3b;
}

.form-item__input:focus ~ .form-item__placeholder {
  bottom: 38px;
  font-size: 12px;
}

.tabs-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 35px;
}

.tabs-nav__item {
  margin: 0 15px;
  position: relative;
}

.tabs-nav__item::before {
  content: '';
  position: absolute;
  height: 3px;
  width: 0;
  display: block;
  background-color: #fff;
  bottom: -8px;
  left: 0;
  transition: .3s width;
}

.tabs-nav__item.active::before {
  width: 100%;
}

.tabs-nav__link {
  font-size: 20px;
  color: #fff;
  letter-spacing: 0;
  line-height: 1.4;
  text-transform: uppercase;
}

.tabs-content__item {
  display: none;
}

.tabs-content__item.active {
  display: block;
}

.btn {
  -webkit-appearance: none;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;
  height: 50px;
  font-size: 20px;
  color: #fff;
  text-transform: uppercase;
  border-radius: 5px;
  transition: .3s background-color linear, .3s color linear;
}

.btn:hover {
  cursor: pointer;
}

.btn-green {
  width: 100%;
  background-color: #009d51;
}

.btn-green:hover {
  background-color: #00cfff;
}

.btn-yellow {
  background-color: #ff6b00;
  max-width: 210px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.btn-yellow:hover {
  background-color: #cfcc00;
}

.btn-orange {
  background-color: #ffeb3c;
  width: 100%;
  color: #000;
}

.btn-orange:hover {
  background-color: #e800df;
  color: #fff;
}

.checkbox__input {
  display: none;
}

.checkbox__input:checked ~ .checkbox__checkmark {
  background-color: #3b3b3b;
}

.checkbox__label:hover {
  cursor: pointer;
}

.checkbox__text {
  font-size: 14px;
  color: #fff;
}

.checkbox__checkmark {
  width: 18px;
  height: 18px;
  margin: 0 4.5px 0 0;
  display: inline-block;
  background: #fff;
  border: 2px solid rgba(0, 0, 0, 0.25);
  position: relative;
  border-radius: 2px;
  transition: .3s background-color linear;
}

.forgot-password {
  font-size: 14px;
  color: #c9c9c9;
  transition: .3s color;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  margin: 10px 0;
}

.forgot-password:hover {
  color: #ffffff;
}

.welcome {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.welcome__banner img {
  max-height: 78px;
}

.welcome__text {
  padding: 0 40px;
  font-size: 20px;
  color: #fff;
  line-height: 1.2;
  letter-spacing: .04em;
  text-align: center;
}

.welcome__text p + p {
  margin-top: 20px;
}

.welcome__checkbox {
  margin-top: 40px;
  padding: 0 40px;
}

.welcome__checkbox .checkbox__text {
  font-size: 10px;
  vertical-align: super;
}

.welcome__coffee {
  margin-top: 40px;
  width: 100%;
}

p .bold {
  font-weight: 700;
}

p .crossed {
  text-decoration: line-through;
}

p .text-small {
  font-size: 15px;
}

.payment-block {
  padding: 20px 30px 30px 30px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
}

.payment-block__title {
  font-size: 22px;
  line-height: 1.2;
  letter-spacing: .04em;
  color: #fff;
  text-align: center;
}

.payment-block__img {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.payment-block__img img {
  max-height: 150px;
}

.payment-block__btn {
  margin-top: 20px;
}

.header {
  background-color: #194D4D;
  flex-shrink: 0;
}

.header__wrapper {
  padding: 50px 0;
}

.header__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo {
  display: flex;
  flex-direction: column;
}

.logo__img {
  max-height: 50px;
  width: 100%;
  object-fit: contain;
}

.logo__text {
  font-size: 19px;
  font-family: 'Bebas Neue', sans-serif;
  line-height: 1.6;
  letter-spacing: 0;
  opacity: .65;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
}

.footer {
  flex-shrink: 0;
  background-color: #303030;
  height: 100px;
  padding-top: 40px;
}

.footer-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.footer-list__item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-list__link {
  font-size: 16px;
  color: #fff;
  text-transform: uppercase;
  transition: .3s color linear;
}

.footer-list__link_faq:hover {
  color: #fffc00;
}

.footer-list__link_contacts:hover {
  color: #00ff83;
}

.footer-list__link_offer:hover {
  color: #ff6b00;
}

.footer-list__link_policy:hover {
  color: #00cfff;
}

@media screen and (max-width: 600px) {
  .footer-list {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    row-gap: 10px;
  }
  .footer-list__item:nth-child(3) {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

@media screen and (max-width: 450px) {
  .payment-block {
    padding: 20px 0;
  }
  .welcome__text {
    padding: 0;
  }
  .welcome__text p {
    margin: 0 auto;
  }
  .welcome__text p:nth-child(1) {
    max-width: 210px;
  }
  .welcome__text p:nth-child(2) {
    max-width: 215px;
  }
  .welcome__text p:nth-child(3) {
    max-width: 182px;
  }
  .footer {
    padding-top: 20px;
  }
  .footer-list {
    row-gap: 40px;
    column-gap: 20px;
  }
  .footer-list__item:nth-child(1) {
    justify-content: flex-end;
  }
  .footer-list__item:nth-child(2) {
    justify-content: flex-start;
  }
  .footer-list__item:nth-child(3) {
    grid-column-start: 1;
    grid-column-end: 3;
    justify-content: center;
  }
  .footer-list__link {
    font-size: 10px;
  }
  .form__content {
    padding: 0 10px;
  }
  .btn {
    height: 45px;
    font-size: 16px;
  }
  .btn-yellow {
    max-width: 100%;
  }
  .btn-orange {
    max-width: 190px;
    margin-right: auto;
    margin-left: auto;
  }
  .welcome__banner img {
    max-height: 75px;
  }
  .welcome__coffee {
    margin-top: 30px;
  }
  .welcome__checkbox {
    padding: 0;
    margin-top: 25px;
  }
  .payment-block__title {
    font-size: 20px;
  }
  .payment-block__img img {
    width: 100%;
    max-height: 110px;
    object-fit: contain;
  }
}
