.footer {
  flex-shrink: 0;
  background-color: rgb(48, 48, 48);
  height: 100px;
  padding-top: 40px;
  &-list {
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    &__item{
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__link {
      font-size: 16px;
      color: #fff;
      text-transform: uppercase;
      transition: .3s color linear;

      &_faq {
        &:hover {
          color: rgb(255, 252, 0);
        }
      }

      &_contacts {
        &:hover {
          color: rgb(0, 255, 131);
        }
      }

      &_offer {
        &:hover {
          color: rgb(255, 107, 0);
        }
      }

      &_policy {
        &:hover {
          color: rgb(0, 207, 255);
        }
      }
    }
  }
}